body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#jakeTitleCard {
  color:hsl(34, 100%, 50%);
  font-size: 250%;
  font-family: Brush Script MT, cursive;
}

#buttonLeft {
  margin-right:5px
}

#buttonRight {
  margin-left:5px
}

#aboutHeader {
  color:rgb(255, 153, 0);
  font-size: 125%;
}

#factsHeader {
  color:hsl(56, 100%, 50%);
  font-size: 125%;
}

#skillsHeader {
  color:rgb(72, 255, 0);
  font-size: 125%;
}

#aboutProjectsHeader{
  color:rgb(255, 153, 0);
  font-size: 200%;
}

#filmStyle{
  color:hsl(192, 100%, 50%);
}

#homeStyle{
  color:hsl(123, 100%, 50%);
}

#startStyle{
  color:hsl(123, 100%, 50%);
  font-size: 200%;
  font-family: "Helvetica Neue";
}

#jakeHeaderStyle{
  color:hsl(34, 100%, 50%);
  font-size: 120%;
}

#pcLink{
  color:rgb(255, 255, 255);
  font-size: 25;
}

.imageAdjuster{
  width:30%;
  height:30%;
}

.imageAdjuster2{
  width:50%;
  height:50%;
}

.imageAdjuster3{
  width:25%;
  height:25%;
}

#borderColorGreen{
  border:5px solid hsl(118, 100%, 50%);
}

#borderColorBlue{
  border:5px solid hsl(175, 100%, 50%);
}

#borderColorYellow{
  border:5px solid hsl(71, 100%, 50%);
}

#borderColorPink{
  border:5px solid hsl(300, 100%, 50%);
}

#borderColorOrange{
  border:5px solid hsl(34, 100%, 50%);
}

.movieButton{
  border: none;
  background-color: inherit;
  
  font-size: 100%;
  color: hsl(192, 100%, 50%);
  cursor: pointer;
  display: inline-block;
  text-decoration: underline;
}